<template>
  <div class="wb-item-wrap">
    <link rel="stylesheet" href="https://use.fontawesome.com/releases/v5.0.10/css/all.css" integrity="sha384-+d0P83n9kaQMCwj8F4RJB66tzIwOKmrdb46+porD/OvrJ+37WqIM7UoBtwHO6Nlg" crossorigin="anonymous">
      <el-card class="box-card">
      <div slot="header" class="clearfix">
        <!-- 头部 -->
        <header>
        <div class="avatar-border"><a :href="'profile/' + user_id"><div class="avatar"></div></a></div>
        
        <span>{{nickname}}</span>
        <span>{{time}}</span>
      </header>
      </div>
      <!-- 正文 -->
      <article>
        {{content}}

        <!-- 九宫格 -->
        <div v-if="imgs.length > 0"></div>
        <!-- 转发 -->
        <div v-if="ori">
        WTF
        </div>
      </article>
      <!-- 尾部 -->
      <footer>
        <a href="#/compose/repost" class="foot-btn"><div class="foot-btn m-box-center-a" id="repost-btn"><i class="fas fa-share-square"></i><h4>{{num_repost}}</h4></div></a>
        <a href="#/compose/comment" class="foot-btn"><div class="foot-btn m-box-center-a" id="comment-btn"><i class="far fa-comment-alt"></i><h4>{{num_comment}}</h4></div></a>
        <div class="foot-btn m-box-center-a" id="like-btn" v-on:click="like"><i class="far fa-thumbs-up"></i><h4>{{num_like}}</h4></div>
        <aside class="btn" v-on:click="more"><i class="fas fa-ellipsis-h"></i></aside>
      </footer>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'SingleStatus',
  data () {
    return {
      success: false
    }
  },
  props: ['msg', 'num_repost', 'num_comment', 'num_like', 'nickname', 'time', 'weibo_id', 'user_id', 'content', 'ori', 'imgs'],
  methods: {
    like: function (e) {
      this.num_like ++;
      this.success = true;
      var xhr = new XMLHttpRequest();
      xhr.open('GET', '/new/like?msgId=' + this.weibo_id);
      xhr.onreadystatechange = function () {
        this.num_like++;
        this.success = true;
      }
      xhr.send();
    },
    more: function (e) {
      this.$emit('more-clicked', this.weibo_id); // 传递关闭事件
    },
  }
}
</script>

<style scoped>
.foot-btn {
  color: rgba(40,47,60,0.8);
  float: left;
}
.foot-btn+.foot-btn {
  margin-left: 1.6875rem;
}
h4 {
  font-size: .8125rem;
    display: inline-block;
    margin-top: 0;
    margin-left: .25rem;
}
.m-avatar-box .m-img-box {
    border-radius: 50%;
    position: relative;
}
.m-box-center {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
}


/* 定义整个card */
.wb-item-wrap {
  overflow: hidden;
  float: bottom;
}

.avatar-border {
    width: 31px;
    height: 31px;
    background-color: gray;
    border-radius: 50% 50% ;
    overflow: auto;
}
.avatar {
    width: 30px;
    height: 30px;
    border-radius: 50% 50% ;
    background-image: url(../assets/logo.png);
    background-size: cover;
}
aside {
  float: right;
}

</style>



