<template>
  <div class="dialog">
      <div class="wrapper" v-if="visible" v-on:click="close_modal">
        <div class="bottom-dialog" v-show="visible">
            <ul class="bottom-list">
                <li :v-on:click="favorite"><a>收藏</a></li>
                <li :v-on:click="report"><a>举报</a></li>
            </ul>
            <ul class="bottom-list">
                <li v-on:click="close"><a>取消</a></li>
            </ul>
        </div>
      </div>
  </div>
</template>

<script>
import SingleStatusVue from './SingleStatus.vue';
export default {
  name: 'BottomDialog',
  data () {
    return {
      visible: false
    }
  },
  props: {
      title: String,
      visible: {
        type: Boolean,
        default: false
      }
    },
  methods: {
    favorite: function (e) {
        this.$emit('favorite-clicked', this.weibo_id);
        alert('WTF');
    },
    report: function (e) {
        alert('WTF');
    },
    close() {
        this.$emit('update:visible', false); // 传递关闭事件
    },
    close_modal(e) {
        if (this.visible) {
            document.querySelector('.bottom-dialog').contains(e.target) ? '' : this.close(); // 判断点击的落点在不在dialog对话框内，如果在对话框外就调用this.close()方法关闭对话框
        }
    }
  },
  components: {
      'item': SingleStatusVue
  }
}
</script>

<style scoped>
.wrapper {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    overflow: hidden;
    outline: 0;
    -webkit-overflow-scrolling: touch;
    background-color: rgb(0, 0, 0);  
    filter: alpha(opacity=60);  
    background-color: rgba(0, 0, 0, 0.6); 
    z-index: 9999;
}

.bottom-dialog {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    
    z-index: 10000;
}
.bottom-list {
    padding-bottom: 1em; 
    background-color: #fff;
}
li {
    list-style:none;
    text-align: center;
}
</style>



