<template>
  <div class="compose">
      <link rel="stylesheet" href="https://unpkg.com/element-ui/lib/theme-chalk/index.css">
        <link rel="stylesheet" href="https://use.fontawesome.com/releases/v5.0.10/css/all.css" integrity="sha384-+d0P83n9kaQMCwj8F4RJB66tzIwOKmrdb46+porD/OvrJ+37WqIM7UoBtwHO6Nlg" crossorigin="anonymous">

      <el-container>


          <!-- header -->
          <el-header>
              <header class="top-bar">
            <div class="top-btn-wrapper">
                <a href="home"><i class="fas fa-chevron-left" style="font-size: 1.5rem; float:left;"></i></a>
            </div>
            <div class="top-btn-wrapper">
                <div class="title" v-if="this.$route.path.match('/compose/repost')">转发微博</div>
                <div class="title" v-else-if="this.$route.path == '/compose/comment'">评论微博</div>
                <div class="title" v-else>发布微博</div>
            </div>
            <div class="top-btn-wrapper">
                <div v-on:click="send" style="float: right;"><a class="send-btn" v-bind:class="{disabled: message.length==0}">发送</a></div>
            </div>
            </header>
            </el-header>

            <!-- main -->
          <el-main>
            <el-input class="post"
                type="textarea"
                :autosize="{minRows: 10, maxRows: 10}"
                placeholder="请输入内容"
                v-model="message">
            </el-input>
            <img v-if="img != ''" :src="img" class="compose-img">
              </el-main>


              <!-- footer -->
            <el-footer>
              <footer class="foot-bar">
                    <!-- 图片 -->
                    <div class="footer-btn">
                        <label for="selectphoto"><i class="far fa-image"></i></label>
                    </div>
                    <!-- 表情 -->
                    <div class="footer-btn">
                        <i class="far fa-smile"></i>
                    </div>
                    <span class="limit" v-if="message.length > 0">{{message.length}}</span>
              </footer>
            </el-footer>
          </el-container>

          <form id="hidden-input" style="display:none;" method="post" enctype="multipart/form-data">
            <input type="text" name="text" id="text"/>
            <input id="selectphoto" name="uploader" type="file" accept="image/gif,image/jpeg,image/webp,image/jpg,image/png,image/bmp" @change="new_upload">
          </form>
      
    </div>
</template>

<script>
export default {
  name: 'Compose',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      message: '',
      img: '',
      has_ori: false,
      ori: {
        user_id: '',
        nickname: '',
        weibo_id: ''
      }
    }
  },
  methods: {
        new_upload: function (e) {
            var _this = this;
            var file = e.target.files[0];
            var reader = new FileReader();
            reader.onload = (function (file) {
                return function (e) {
                    _this.img = this.result;
                };
            })(e.target.files[0]);
            reader.readAsDataURL(e.target.files[0]);
        },
        send: function (e) {
            if(this.$route.path == '/compose/repost/') {
                document.getElementById('hidden-input').setAttribute('action', '/new/repost');
                
            }
            else if(this.$route.path == '/compose/comment/') {
                document.getElementById('hidden-input').setAttribute('action', '/new/comment');
            }
            else {
                document.getElementById('hidden-input').setAttribute('action', '/new/compose');
            }
            
            document.getElementById('text').value = this.message;
            document.getElementById('hidden-input').submit();
            alert(document.getElementById('text').value)
        }
    }
}
</script>

<style scoped>
header.top-bar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 2.75rem;
  width: 100%;
  box-sizing: border-box;
  padding: 0;
  background-color: #fafafa;
  color: #262626;
  border-top-width: 0;
  border-right-width: 0;
  border-bottom-width: 1px;
  border-left-width: 0;
  border-color: #cfcfcf;
  border-style: solid;
  font-size: 1rem;
  z-index: 995;
}
.top-btn-wrapper {
    float: left;
    width: 33.3%;
    height: 100%;
}
.title {
    position: absolute;
    top: 50%;
    left: 50%;
}
.footer-btn {
    float: left;
}
.limit {
    float: right;
}
.send-btn {
    min-width: 0;
    margin-left: .75rem;
    margin-right: .001rem;
    height: 1.6875rem;
    line-height: 1.6875rem;
    color: #1b9af4;
}
.send-btn.disabled {
    opacity: .5;
}
.post {
    font-size: 1.5rem;
    border: none;
}
.post:focus {
    outline: none;
}

.img-wrapper {
    display: flex;
    flex-flow: wrap;
}
.compose-img {
    position: absolute;
    left: 0;
    width: 115px;
    height: 115px;
    -o-object-fit: cover;
    object-fit: cover;
}

.foot-bar {
  position: fixed;
  bottom: 0;
  height: 2.75rem;
  font-size: 1rem;
  z-index: 995;
}


/* 九宫格 */
.grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr); /* 相当于 1fr 1fr 1fr */
  grid-template-rows: repeat(3, 1fr); /* fr单位可以将容器分为几等份 */
  grid-gap: 1%; /* grid-column-gap 和 grid-row-gap的简写 */
  grid-auto-flow: row;
}
.compose-img-wrapper{
  color: #fff;
  font-size: 50px;
  line-height: 2;
  text-align: center;
}
.close-btn {
    position: absolute;
    top: 0;
    right: 0;
    background-color: #000;
    opacity: .4;
    padding: 0;
    border: 0;
    outline: none;
    z-index:2;
}
.m-rpic-c {
    overflow: hidden;
    color: #fff;
    font-size: 1.5rem;
    width: 1.3125rem;
    height: 1.3125rem;
}
.m-style-svg {
    display: inline-block;
    fill: currentcolor;
    max-width: 100%;
    position: relative;
    vertical-align: text-bottom;
    -webkit-user-select: text;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
.m-flex-grow1 {
    -webkit-box-flex: 1;
    -ms-flex-positive: 1;
    flex-grow: 1;
}

</style>



